}
.header-middle__menu-item {
    padding: 13px 12px;
}
.header-menu__item {
    font-size: 14px;
    line-height: 16px;
    padding: 8px 12px;
}
.header-menu__item--active {
    color: #fff;
    background: rgba(82, 87, 92, 0.4);
}

.main-content__filters-list {
    padding: 8px;
    -webkit-box-shadow: 0px 16px 32px rgba(0, 0, 0, 0.24);
            box-shadow: 0px 16px 32px rgba(0, 0, 0, 0.24);
    border-radius: 4px;
