}
.main-content__wrap {
    width: 1312px;
}
.main-content__title {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 30px;
    line-height: 37px;
    color: #fff;
    padding-bottom: 24px;
}
.main-content__filter-search {
    width: auto;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
}
.main-content__filter-location {
