:root {
    --black-color:  #25282B;
    --dark-color: #52575C;
    --light-white: #CACCCF;

    --dark-green: #4B9C50;
    --middle-green:  #75BD7A;
    --light-green: #53AD59;

    --gray1: #E8E8E8;
    --gray2:  #A0A4A8;
    --gray3:#F3F4F6;

    --BG_IMG: url(/static/img/BG.89f4acf.png);
    --plus: url(data:image/svg+xml;base64,);
    --enter: url(data:image/svg+xml;base64,);
}

.ui-divider--vertical {
    height: 100%;
    width: 1px;
    background: #E8E8E8;
}

.plus-icon {
    background: var(--plus);
    background-repeat: no-repeat;
    background-position: center;
    width: 20px;
    height: 20px;
    display: block;
}

.enter-icon {
    background: var(--enter);
    background-repeat: no-repeat;
    background-position: center;
    width: 20px;
    height: 20px;
    display: block;
}

.ui-divider--horizontal {
    width: 100%;
    height: 1px;
    background: #E8E8E8;
}

a,
a:hover,
a:visited,
a:active {
    text-decoration: none;
    color: inherit;
}

* {
    font-family: 'Roboto';
}

ul, menu, li {
    list-style: none;
    margin: 0;
    padding: 0;
}

.ui-button {
    padding: 7px 15px;
    min-width: 144px;
    text-align: center;
    border-radius: 3px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.ui-button--primary {
    background: var(--light-green);
    color: #fff;
    border: none;
    border: 1px solid rgba(0, 0, 0, 0.06);
}
.ui-button--primary:hover {
    background: var(--middle-green);
    border: 1px solid rgba(0, 0, 0, 0.06);
    -webkit-box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.2);
            box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.2);
}
.ui-button--primary:active {
    background: var(--dark-green);
}
.ui-button--disabled {
    background: var(--gray1);
    border: 1px solid rgba(0, 0, 0, 0.06);
}
.ui-button--outline {
    border: 1px solid var(--middle-green);
    background: transparent;
    color: #25282B;
}
.ui-button--outline:hover,
.ui-button--outline:active {
    border: 1px solid var(--middle-green);
    background: transparent;
    color: var(--light-green);
    -webkit-box-shadow: none;
            box-shadow: none;
}
.ui-button--transparent {
    background: none;
    outline: none;
    border: none;
}
.ui-button__icon {
    margin-right: 7px;
}


.ui-input input {
  width: 100%;
  border: none;
}
.ui-input input:focus {
  border: none;
  outline: none;
}

